@use "sass:map";

$breakpoints: (
  "xs": 420px,
  "sm": 640px,
  "md": 900px,
  "lg": 1200px,
);

@mixin xs {
  @media (max-width: map-get($breakpoints, "xs")) {
    @content;
  }
}
@mixin sm {
  @media (max-width: map-get($breakpoints, "sm")) {
    @content;
  }
}
@mixin md {
  @media (max-width: map-get($breakpoints, "md")) {
    @content;
  }
}
@mixin lg {
  @media (max-width: map-get($breakpoints, "lg")) {
    @content;
  }
}

//Style a specific breakpoint not covered by the ones above

@mixin breakpoint($bp: 0) {
  @media (max-width: $bp) {
    @content;
  }
}
